.hero-nav {
    z-index: 100;
    position: fixed;
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
    background-color: #fff;
    //border-bottom: 1px solid #dcdfe6;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
    &__logo {
        cursor: pointer;
        margin-left: 5%;
        width: 185px;
        & svg {
            width: 185px;
        }
    }
    &__blank {
        flex-grow: 1;
    }
    &__icon {
        margin-right: 45px;
        & svg {
            cursor: pointer;
            &:hover {
                filter: brightness(0.95);
            }
            &:active {
                filter: brightness(0.9);
            }
        }
    }
    &__links {
        display: flex;
        justify-content: flex-end;
        margin-right: 35px;
        position: relative;
         ::after {
            content: '';
            display: block;
            position: absolute;
            left: 103%;
            top: 25%;
            bottom: 25%;
            width: 2px;
            background-color: rgba(0, 0, 0, 0.05);
        }
        & a {
            white-space: nowrap;
            -webkit-appearance: none;
            color: #777777;
            font-size: 1.8rem;
            font-weight: 700;
            letter-spacing: 2px;
            line-height: 2.5rem;
            padding: 10px 15px;
            border-radius: 5px;
            margin-right: 25px;
            transition: all 200ms cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
            &.selected {
                background-color: rgba(0, 0, 0, 0.05);
                color: #777777;
                &:hover {
                    background-color: rgba(0, 0, 0, 0.05);
                }
            }
            &:hover {
                background-color: rgba(0, 0, 0, 0.05);
                text-decoration: none;
            }
            &:active {
                background-color: rgba(0, 0, 0, 0.1);
                text-decoration: none;
            }
        }
    }
}

.hero-route-wrapper {
    padding-top: 82px;
    height: 100vh;
    &--scroll {
        height: 100%;
        overflow-y: scroll;
    }
}

