@keyframes PopoverSVG {
    0% {
        transform: translateY(-25%);
        opacity: 0;
    }
    100% {
        transform: translateY(-0);
        opacity: 1;
    }
}

@keyframes fadeInDown {
    from {
        opacity: 0;
        transform: translate3d(0, -25%, 0);
    }
    to {
        opacity: 1;
        transform: translate3d(0, 0, 0);
    }
}

@keyframes fadeInDownItem {
    from {
        opacity: 0;
        transform: translate3d(0, -45%, 0);
    }
    to {
        opacity: 1;
        transform: translate3d(0, 0, 0);
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

#BackSvg {
    animation-name: fadeInDown;
    animation-duration: 1.2s;
}

#Popover {
    animation-name: fadeInDownItem;
    animation-duration: 1.6s;
}

#Card {
    animation-name: fadeInDownItem;
    animation-duration: 1.6s;
}

#Line2 {
    animation-name: fadeInDownItem;
    animation-duration: 1.2s;
}

#Line1 {
    animation-name: fadeInDownItem;
    animation-duration: 1.8s;
}

#Line3 {
    animation-name: fadeInDownItem;
    animation-duration: 2.4s;
}

#Line4 {
    animation-name: fadeInDownItem;
    animation-duration: 3s;
}

#ButtonOrder {
    animation-name: fadeInDownItem;
    animation-duration: 1.8s;
}

#Button2 {
    animation-name: fadeInDownItem;
    animation-duration: 2.4s;
}

.home-section {
    margin: 55px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    &__card {
        display: flex;
        flex-direction: column;
        align-items: center;
        cursor: pointer;
        margin: 0 40px;
        width: 245px;
        height: 300px;
        border-radius: 12px;
        border-style: solid;
        transition: all 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
        padding: 11px 15px;
        color: #777777;
        background-color: rgb(255, 255, 253);
        border-color: rgb(229, 229, 229);
        border-width: 2px 2px 4px;
        & .image {
            width: 125px;
            height: 100px;
            margin: 45px 0;
            text-align: center;
            line-height: 35px;
            color: #afafaf;
            transition: color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
            & svg {
                width: 125px;
                height: 100px;
                & path {
                    transition: all 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
                }
            }
        }
        & .tittle {
            font-size: 1.8rem;
            font-weight: 600;
            margin-bottom: 10px;
            filter: brightness(1.2);
        }
        & .info {
            text-align: center;
            font-size: 1.5rem;
            font-weight: 400;
            line-height: 2.2rem;
            margin-bottom: 10px;
            padding: 0 18px;
            filter: brightness(1.2);
        }
        &:hover {
            background-color: #f7f7f7;
        }
        &:active {
            color: #1899d6;
            border-color: #84d8ff;
            background-color: #ddf4ff;
            border-width: 2px;
            margin-bottom: 0px;
            transform: translateY(2px) translateZ(0);
            & svg path {
                stroke: #84d8ff;
            }
            & .image {
                color: #84d8ff;
            }
        }
    }
}

.home-footer {
    width: 100%;
    display: flex;
    background-color: #f7fbfd;
    height: 340px;
    padding: 55px 150px;
    &__column {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        & svg {
            width: 36px;
        }
        &--work {
            flex-grow: 1;
        }
    }
    &__tittle {
        font-size: 1.8rem;
        font-weight: 700;
        color: #4b4b4b;
        margin-bottom: 15px;
    }
    &__link {
        color: #777777;
        font-size: 1.6rem;
        font-weight: 500;
        &:hover {
            text-decoration: none;
            color: #4b4b4b;
        }
    }
    &__message {
        color: #4b4b4b;
        font-size: 1.6rem;
        font-weight: 500;
        display: flex;
        align-items: center;
        &:hover {
            & svg {
                transform: scale(1.3);
            }
        }
        & svg {
            transition: all 200ms cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
            width: 20px;
        }
        & span {
            margin-left: 12px;
        }
    }
}

.components-layout {
    height: 100vh;
    width: 100%;
    display: flex;
    padding-top: 82px;
    &__sidebar {
        &::-webkit-scrollbar {
            width : 3px;  /*高宽分别对应横竖滚动条的尺寸*/
        }
        &::-webkit-scrollbar-thumb {
            /*滚动条里面小方块*/
            border-radius:3px;
            background-color: #e5e5e5;
        }
        &::-webkit-scrollbar-track {
            background   : #f7f7f7;
        }
        height: 100%;
        overflow-y: scroll;
    }
    &__content {
        height: 100%;
        flex-grow: 1;
        overflow-y: scroll;
    }
}

.sidebar {
    padding: 0 0 60px;
    margin: 0;
    display: flex;
    width: 280px;
    flex-direction: column;
}

.sidebar-item {
    background-color: #fff;
    display: inline-block;
    padding: 10px 30px 10px 45px;
    white-space: nowrap;
    color: #4b4b4b;
    font-size: 1.4rem;
    font-weight: 500;
    letter-spacing: .8px;
    cursor: pointer;
    transition: color 0.2s ease-in 50ms, background-color 0.2s ease-in 50ms;
    text-align: left;
    width: 100%;
    position: relative;
    &--low {
        padding-left: 68px;
    }
    &-tittle {
        margin-top: 10px;
        padding: 8px 30px 8px 45px;
        font-size: 1.6rem;
        font-weight: 700;
        color: rgba(0, 0, 0, 0.8);
    }
    &-sign {
        padding: 8px 0 8px 45px;
        font-size: 1.2rem;
        font-weight: 700;
        color: rgba(0, 0, 0, 0.45);
    }
    &::after {
        content: '';
        display: block;
        position: absolute;
        width: 4px;
        left: 276px;
        top: 50%;
        bottom: 50%;
        border-radius: 2px;
        background-color: #1cb0f6;
        opacity: 0;
        transition: all 300ms cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
    }
    &--active {
        & .sidebar-item {
            background-color: #ddf4ff;
            color: #1cb0f6;
            &::after {
                top: 0;
                bottom: 0;
                opacity: .85;
            }
        }
    }
    &:hover {
        color: #1cb0f6;
    }
}

